"use client";

import { cn } from "@/lib/utils";

import { useState } from "react";
import { Button } from "./ui/button";

export function Cart() {
  const [quantity, setQuantity] = useState<number>(1);

  return (
    <>
      <section className="mt-10 pb-10 border-b border-primary border-opacity-[14%] ">
        <div className="flex items-center justify-between pb-3 border-b border-primary border-opacity-[14%] mb-8">
          <span className="text-primary font-body text-sm">PRODUCT</span>
          <span className="text-primary font-body text-sm">TOTAL</span>
        </div>

        <div className="flex items-start">
          <img
            src="/svg/duckstore.svg"
            width={68}
            height={68}
            alt="A yellow rubber duck."
          />
          <div className="ml-2">
            <h6 className="shrink-0 text-sm text-primary font-body font-[500]">
              asdasd
            </h6>
            <span className="text-xs text-primary font-body font-[500]">
              ${5} USD
            </span>
          </div>

          <span className="ml-auto text-lg font-body text-main font-[600]">
            ${(5 * quantity).toFixed(2)}
          </span>
        </div>

        <div className="flex items-center gap-2.5 mt-5">
          <span className="text-xs text-primary font-body font-[500]">
            Quantity:
          </span>
          <ul className="flex items-center gap-[10px]">
            {[4, 10, 20, 100].map((qty) => (
              <li key={qty}>
                <button
                  className={cn(
                    "bg-[#3D3C3F]  font-body rounded-sm w-10 h-[30px] flex items-center justify-center hover:bg-main text-[12px] text-white",
                    quantity === qty ? "bg-main" : ""
                  )}
                  type="button"
                  onClick={() => {
                    if (quantity === qty) {
                      setQuantity(1);
                    } else {
                      setQuantity(qty);
                    }
                  }}
                >
                  {qty}
                </button>
              </li>
            ))}
          </ul>
        </div>
      </section>

      <section className="mt-5 space-y-3">
        <div className="flex items-center justify-between">
          <p className="font-body text-main text-sm font-[700]">
            Estimated Total
          </p>
          <span className="font-body text-main text-sm font-[600]">
            ${(5 * quantity).toFixed(2)}
          </span>
        </div>
        <p className="text-[11px] text-primary font-[500]">
          Taxes, discounts and shipping calculated at checkout
        </p>
      </section>

      <Button className="mt-12" onClick={() => {}}>
        Check out
      </Button>
    </>
  );
}
